import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showGalMenu: false,
    showColMenu: false,
    gaalsien: {
      name: 'Sandskimmer',
      desc: 'Light attack vehicle.',
      speed: 130,
      armor: 0,
      damage: 15,
      rof: 'Medium',
      hull: 650,
      strong: 'Strikecraft, Railguns',
      weak: 'AAVs, Cruisers',
      image: 'sandskimmer_gaalsien.jpg',
      speedHigher: true,
      armorHigher: null,
      damageHigher: null,
      hullHigher: true,
    },
    coalition: {
      name: 'LAV',
      desc: 'Light attack vehicle.',
      speed: 110,
      armor: 0,
      damage: 15,
      rof: 'Medium',
      hull: 550,
      strong: 'Strikecraft, Railguns',
      weak: 'Assault Railguns, Cruisers',
      image: 'LAV_coalition.jpg',
      speedHigher: false,
      armorHigher: null,
      damageHigher: null,
      hullHigher: false,
    }
  },
  getters: {
    galStatsChanged: state => {
      return [
        state.gaalsien.name,
        state.gaalsien.speed,
        state.gaalsien.armor,
        state.gaalsien.damage,
        state.gaalsien.hull,
      ]
    }
  },
  mutations: {
    falseGalMenu: state => state.showGalMenu = false,
    trueGalMenu: state => state.showGalMenu = true,
    falseColMenu: state => state.showColMenu = false,
    trueColMenu: state => state.showColMenu = true,
    setColStats (state, payload) {
      state.coalition.name = payload.name,
      state.coalition.speed = payload.speed,
      state.coalition.armor = payload.armor,
      state.coalition.damage = payload.damage,
      state.coalition.hull = payload.hull,
      state.coalition.speedHigher = payload.speedHigher,
      state.coalition.armorHigher = payload.armorHigher,
      state.coalition.damageHigher = payload.damageHigher,
      state.coalition.hullHigher = payload.hullHigher,
      state.gaalsien.speedHigher = payload.gSpeedHigher,
      state.gaalsien.armorHigher = payload.gArmorHigher,
      state.gaalsien.damageHigher = payload.gDamageHigher,
      state.gaalsien.hullHigher = payload.gHullHigher
    },
    setGalStats (state, payload) {
      state.gaalsien.name = payload.name,
      state.gaalsien.speed = payload.speed,
      state.gaalsien.armor = payload.armor,
      state.gaalsien.damage = payload.damage,
      state.gaalsien.hull = payload.hull,
      state.gaalsien.speedHigher = payload.speedHigher,
      state.gaalsien.armorHigher = payload.armorHigher,
      state.gaalsien.damageHigher = payload.damageHigher,
      state.gaalsien.hullHigher = payload.hullHigher
    },
  },
});
