<template>
  <section class="gaalsien">
    <header>
      <transition name="slide-fade" mode="out-in">
        <div class="menu-a" v-if="this.$store.state.showGalMenu == false">
          <button class="menu-a-button" @click="trueGalMenu()">
            <h2>Select Coalition unit</h2>
            <img src="@/assets/arrow_drop_down-gray.svg">
          </button>
        </div>
        <div class="menu-b" v-else>
          <button class="menu-a-button" @click="falseGalMenu()">
            <h2>Collapse</h2>
            <img src="@/assets/arrow_drop_up-gray.svg">
          </button>
        </div>
      </transition>
      <transition name="vertical-fade" mode="out-in">
        <ul class="list-grid    gal-list-grid" v-if="this.$store.state.showGalMenu" >
          <li v-for="(unit, index) in componentArray" @click="activeUnitSwap(unit), fillData()">
            <div class="list-item" ><h5> {{ unit }} </h5></div>
          </li>
        </ul>
      </transition>
    </header>
    <component :is="currentComponent"/>
    <div class="chart" id="galChart">
      <galChart :chart-data="datacollection" />
    </div>
  </section>
</template>

<script>
import sandskimmer from "@/components/gaalsienUnits/sandskimmer.vue";
import gaalsienCarrier from "@/components/gaalsienUnits/gaalsienCarrier.vue";
import assaultRailgun from "@/components/gaalsienUnits/assaultRailgun.vue";
import assaultShip from "@/components/gaalsienUnits/assaultShip.vue";
import gaalsienBaserunner from "@/components/gaalsienUnits/gaalsienBaserunner.vue";
import gaalsienSalvager from "@/components/gaalsienUnits/gaalsienSalvager.vue";
import heavyRailgun from "@/components/gaalsienUnits/heavyRailgun.vue";
import honorguardCruiser from "@/components/gaalsienUnits/honorguardCruiser.vue";
import interceptor from "@/components/gaalsienUnits/interceptor.vue";
import missileShip from "@/components/gaalsienUnits/missileShip.vue";
import precisionBomber from "@/components/gaalsienUnits/precisionBomber.vue";
import productionCruiser from "@/components/gaalsienUnits/productionCruiser.vue";
import siegeCruiser from "@/components/gaalsienUnits/siegeCruiser.vue";
import galChart from '@/components/charts/galChart.vue';

export default {
  name: "gaalsien",
  mounted () {
    this.fillData();
  },
  components: {
    'Sandskimmer': sandskimmer,
    'Carrier': gaalsienCarrier,
    'Assault-Railgun': assaultRailgun,
    'Assault-Ship': assaultShip,
    'Baserunner': gaalsienBaserunner,
    'Salvager': gaalsienSalvager,
    'Heavy-Railgun': heavyRailgun,
    'Honorguard-Cruiser': honorguardCruiser,
    'Interceptor': interceptor,
    'Missile-Ship': missileShip,
    'Precision-Bomber': precisionBomber,
    'Production-cruiser': productionCruiser,
    'Siege-Cruiser': siegeCruiser,
    'galChart': galChart
  },
  data: function() {
    return {
      currentComponent: sandskimmer,
      datacollection: { },
      componentArray: [
        'Sandskimmer',
        'Carrier',
        'Assault-Railgun',
        'Assault-Ship',
        'Baserunner',
        'Salvager',
        'Heavy-Railgun',
        'Honorguard-Cruiser',
        'Interceptor',
        'Missile-Ship',
        'Precision-Bomber',
        'Production-cruiser',
        'Siege-Cruiser'
      ],
    }
  },
  methods: {
    falseGalMenu () {
      this.$store.commit('falseGalMenu')
    },
    trueGalMenu () {
      this.$store.commit('trueGalMenu')
    },
    activeUnitSwap: function(unit) {
      this.currentComponent = unit;
      this.$store.commit('falseGalMenu')
    },
    fillData() {
      setTimeout(() => {
        this.datacollection = {
          labels: [this.$store.state.gaalsien.name],
          datasets: [
            {
              label: 'Speed',
              backgroundColor: '#226666',
              data: [this.$store.state.gaalsien.speed]
            },
            {
              label: 'Armor',
              backgroundColor: '#2D882D',
              data: [this.$store.state.gaalsien.armor]
            },
            {
              label: 'Damage',
              backgroundColor: '#AA3939',
              data: [this.$store.state.gaalsien.damage]
            },
            {
              label: 'Hull',
              backgroundColor: '#AA6C39',
              data: [this.$store.state.gaalsien.hull]
            },
          ],
        };
      }, 500)
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.gaalsien {
    display: flex;
    flex-direction: column;
    background-color: #AA3939;
    padding-bottom: 10px;
    border-radius: .4em;
}

.gal-list-grid li:hover {
    font-weight: 400;
    background-color: #AA3939;
}

#galChart {
  display: flex;
  justify-content: flex-end;
}

</style>
