<template>
  <section class="unit-container">
    <figure class="unit-image">
      <img alt="battle" src="@/assets/coalitionUnits/gunship_coalition.jpg">
      <figcaption> <h3> {{ this.name }} </h3> </figcaption>
    </figure>
    <div class="unit-description">
      <p> {{ this.desc }} </p>
    </div>
    <div class="unit-stats" id="coalition-stats">
      <div class="stat">
        <div class="stat-title"><strong>Speed:</strong></div>
        <div class="stat-value"> {{ this.speed }} </div>
        <div v-if="this.$store.state.coalition.speedHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.coalition.speedHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.coalition.speedHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Armor:</strong></div>
        <div class="stat-value"> {{ this.armor }} </div>
        <div v-if="this.$store.state.coalition.armorHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.coalition.armorHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.coalition.armorHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Damage:</strong></div>
        <div class="stat-value"> {{ this.damage }} </div>
        <div v-if="this.$store.state.coalition.damageHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.coalition.damageHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.coalition.damageHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Rate of Fire:</strong></div>
        <div class="stat-value"> {{ this.rof }} </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Hull:</strong></div>
        <div class="stat-value"> {{ this.hull }} </div>
        <div v-if="this.$store.state.coalition.hullHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.coalition.hullHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.coalition.hullHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Strong vs:</strong></div>
        <div class="stat-value"> {{ this.strong }} </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Weak vs:</strong></div>
        <div class="stat-value"> {{ this.weak }} </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "gunship",
  mounted: function () {
    this.statCompare();
    this.setColStats();
  },
  data: function() {
    return {
      name: 'Gunship',
      desc: 'Flying ship with a big gun.',
      speed: 375,
      armor: 10,
      damage: 15,
      rof: 'Very high',
      hull: 1050,
      strong: 'Ground units',
      weak: 'Anti-air, Interceptors, Strikecraft',
      speedHigher: null,
      armorHigher: null,
      damageHigher: null,
      hullHigher: null,
      gSpeedHigher: null,
      gArmorHigher: null,
      gDamageHigher: null,
      gHullHigher: null,
    }
  },
  methods: {
    setColStats: function () {
      this.$store.commit('setColStats', {
        name: this.name,
        speed: this.speed,
        armor: this.armor,
        damage: this.damage,
        hull: this.hull,
        speedHigher: this.speedHigher,
        armorHigher: this.armorHigher,
        damageHigher: this.damageHigher,
        hullHigher: this.hullHigher,
        gSpeedHigher: this.gSpeedHigher,
        gArmorHigher: this.gArmorHigher,
        gDamageHigher: this.gDamageHigher,
        gHullHigher: this.gHullHigher,
      });
    },
    statCompare: function () {
      var g = this.$store.state.gaalsien;

      if (this.speed > g.speed) {
        this.speedHigher = true;
        this.gSpeedHigher = false;
      } else if (this.speed < g.speed) {
        this.speedHigher = false;
        this.gSpeedHigher = true;
      } else if (this.speed == g.speed) {
        this.speedHigher = null;
        this.gSpeedHigher = null;
      };

      if (this.armor > g.armor) {
        this.armorHigher = true;
        this.gArmorHigher = false;
      } else if (this.armor < g.armor) {
        this.armorHigher = false;
        this.gArmorHigher = true;
      } else if (this.armor == g.armor) {
        this.armorHigher = null;
        this.gArmorHigher = null;
      };

      if (this.damage > g.damage) {
        this.damageHigher = true;
        this.gDamageHigher = false;
      } else if (this.damage < g.damage) {
        this.damageHigher = false;
        this.gDamageHigher = true;
      } else if (this.damage == g.damage) {
        this.damageHigher = null;
        this.gDamageHigher = null;
      };

      if (this.hull > g.hull) {
        this.hullHigher = true;
        this.gHullHigher = false;
      } else if (this.hull < g.hull) {
        this.hullHigher = false;
        this.gHullHigher = true;
      } else if (this.hull == g.hull) {
        this.hullHigher = null;
        this.gHullHigher = null;
      };
    },
  }
};
</script>

