<template>
  <section class="coalition">
    <header>
      <transition name="slide-fade" mode="out-in">
        <div class="menu-a" v-if="this.$store.state.showColMenu == false">
          <button class="menu-a-button" @click="trueColMenu()">
            <h2>Select Coalition unit</h2>
            <img src="@/assets/arrow_drop_down-gray.svg">
          </button>
        </div>
        <div class="menu-b" v-else>
          <button class="menu-a-button" @click="falseColMenu()">
            <h2>Collapse</h2>
            <img src="@/assets/arrow_drop_up-gray.svg">
          </button>
        </div>
      </transition>
      <transition name="vertical-fade" mode="out-in">
        <ul class="list-grid    gal-list-grid" v-if="this.$store.state.showColMenu" >
          <li v-for="(unit, index) in componentArray" @click="activeUnitSwap(unit), fillData()">
            <div class="list-item"><h5> {{ unit }} </h5></div>
          </li>
        </ul>
      </transition>
    </header>
    <component :is="currentComponent"/>
    <!-- Create a coalition stats component -->
    <div class="chart">
      <colChart :chart-data="datacollection" />
    </div>
  </section>
</template>

<script>
import lav from "@/components/coalitionUnits/LAV.vue";
import coalitionCarrier from "@/components/coalitionUnits/coalitionCarrier.vue";
import aav from "@/components/coalitionUnits/AAV.vue";
import artilleryCruiser from "@/components/coalitionUnits/artilleryCruiser.vue";
import assaultCruiser from "@/components/coalitionUnits/assaultCruiser.vue";
import battlecruiser from "@/components/coalitionUnits/battlecruiser.vue";
import coalitionBaserunner from "@/components/coalitionUnits/coalitionBaserunner.vue";
import coalitionSalvager from "@/components/coalitionUnits/coalitionSalvager.vue";
import gunship from "@/components/coalitionUnits/gunship.vue";
import missileBattery from "@/components/coalitionUnits/missileBattery.vue";
import railgun from "@/components/coalitionUnits/railgun.vue";
import strikeFighter from "@/components/coalitionUnits/strikeFighter.vue";
import supportCruiser from "@/components/coalitionUnits/supportCruiser.vue";
import tacticalBomber from "@/components/coalitionUnits/tacticalBomber.vue";
import colChart from '@/components/charts/colChart.vue';

export default {
  name: "coalition",
  mounted () {
    this.fillData();
  },
  components: {
    'LAV': lav,
    'Carrier': coalitionCarrier,
    'AAV': aav,
    'Artillery-Cruiser': artilleryCruiser,
    'Assault-Cruiser': assaultCruiser,
    'Battlecruiser': battlecruiser,
    'Baserunner': coalitionBaserunner,
    'Salvager': coalitionSalvager,
    'Gunship': gunship,
    'Missile-Battery': missileBattery,
    'Railgun': railgun,
    'Strike-Fighter': strikeFighter,
    'Support-Cruiser': supportCruiser,
    'Tactical-Bomber': tacticalBomber,
    'colChart': colChart,
  },
  data: function() {
    return {
      currentComponent: lav,
      datacollection: { },
      componentArray: [
        'LAV',
        'Carrier',
        'AAV',
        'Artillery-Cruiser',
        'Assault-Cruiser',
        'Battlecruiser',
        'Baserunner',
        'Salvager',
        'Gunship',
        'Missile-Battery',
        'Railgun',
        'Strike-Fighter',
        'Support-Cruiser',
        'Tactical-Bomber'
      ]
    }
  },
  methods: {
    falseColMenu () {
      this.$store.commit('falseColMenu')
    },
    trueColMenu () {
      this.$store.commit('trueColMenu')
    },
    activeUnitSwap: function(unit) {
      this.currentComponent = unit;
      this.$store.commit('falseColMenu');
    },
    fillData() {
      setTimeout(() => {
        this.datacollection = {
          labels: [this.$store.state.coalition.name],
          datasets: [
            {
              label: 'Speed',
              backgroundColor: '#226666',
              data: [this.$store.state.coalition.speed]
            },
            {
              label: 'Armor',
              backgroundColor: '#2D882D',
              data: [this.$store.state.coalition.armor]
            },
            {
              label: 'Damage',
              backgroundColor: '#AA3939',
              data: [this.$store.state.coalition.damage]
            },
            {
              label: 'Hull',
              backgroundColor: '#AA6C39',
              data: [this.$store.state.coalition.hull]
            },
          ],
        };
      }, 500)
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.coalition {
    display: flex;
    flex-direction: column;
    background-color: #226666;
    padding-bottom: 10px;
    border-radius: .4em;
}

.col-list-grid li:hover {
    font-weight: 400;
    background-color: #226666;
}

</style>
