import Vue from "vue";
import Router from "vue-router";
import Header from "@/components/header.vue";
import Gaalsien from "@/views/Gaalsien.vue";
import Coalition from "@/views/Coalition.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      components: {
        header: Header,
        gaalsien: Gaalsien,
        coalition: Coalition,
      }
    }
  ]
});
