<template>
  <section class="unit-container">
    <figure class="unit-image">
      <img alt="battle" src="@/assets/gaalsienUnits/productionCruiser_gaalsien.jpg">
      <figcaption> <h3> {{ this.name }} </h3> </figcaption>
    </figure>
    <div class="unit-description">
      <p> {{ this.desc }} </p>
    </div>
    <div class="unit-stats" id="gaalsien-stats">
      <div class="stat">
        <div class="stat-title"><strong>Speed:</strong></div>
        <div class="stat-value"> {{ this.speed }} </div>
        <div v-if="this.$store.state.gaalsien.speedHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.gaalsien.speedHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.gaalsien.speedHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Armor:</strong></div>
        <div class="stat-value"> {{ this.armor }} </div>
        <div v-if="this.$store.state.gaalsien.armorHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.gaalsien.armorHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.gaalsien.armorHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Damage:</strong></div>
        <div class="stat-value"> {{ this.damage }} </div>
        <div v-if="this.$store.state.gaalsien.damageHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.gaalsien.damageHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.gaalsien.damageHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Rate of Fire:</strong></div>
        <div class="stat-value"> {{ this.rof }} </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Hull:</strong></div>
        <div class="stat-value"> {{ this.hull }} </div>
        <div v-if="this.$store.state.gaalsien.hullHigher == true" class="stat-eval"> <img src="@/assets/arrow_up-green.svg"> </div>
        <div v-else-if="this.$store.state.gaalsien.hullHigher == false" class="stat-eval"> <img src="@/assets/arrow_down-red.svg"> </div>
        <div v-else="this.$store.state.gaalsien.hullHigher == null" class="stat-eval"> <img src="@/assets/line-teal.svg"> </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Strong vs:</strong></div>
        <div class="stat-value"> {{ this.strong }} </div>
      </div>
      <div class="stat">
        <div class="stat-title"><strong>Weak vs:</strong></div>
        <div class="stat-value"> {{ this.weak }} </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "productionCruiser",
  mounted: function () {
    this.statCompare();
    this.setGalStats();
  },
  data: function() {
    return {
      name: 'Production Cruiser',
      desc: 'Produces smaller units.',
      speed: 70,
      armor: 5,
      damage: 20,
      rof: 'Low',
      hull: 4100,
      strong: 'N/A',
      weak: 'Everything',
      speedHigher: null,
      armorHigher: null,
      damageHigher: null,
      hullHigher: null,
      cSpeedHigher: null,
      cArmorHigher: null,
      cDamageHigher: null,
      cHullHigher: null,
    }
  },
  methods: {
    setGalStats: function () {
      this.$store.commit('setGalStats', {
        name: this.name,
        speed: this.speed,
        armor: this.armor,
        damage: this.damage,
        hull: this.hull,
        speedHigher: this.speedHigher,
        armorHigher: this.armorHigher,
        damageHigher: this.damageHigher,
        hullHigher: this.hullHigher,
        cSpeedHigher: this.cSpeedHigher,
        cArmorHigher: this.cArmorHigher,
        cDamageHigher: this.cDamageHigher,
        cHullHigher: this.cHullHigher,
      });
    },
    statCompare: function () {
      var c = this.$store.state.coalition;

      if (this.speed > c.speed) {
        this.speedHigher = true;
        this.cSpeedHigher = false;
      } else if (this.speed < c.speed) {
        this.speedHigher = false;
        this.cSpeedHigher = true;
      } else if (this.speed == c.speed) {
        this.speedHigher = null;
        this.cSpeedHigher = null;
      };

      if (this.armor > c.armor) {
        this.armorHigher = true;
        this.cArmorHigher = false;
      } else if (this.armor < c.armor) {
        this.armorHigher = false;
        this.cArmorHigher = true;
      } else if (this.armor == c.armor) {
        this.armorHigher = null;
        this.cArmorHigher = null;
      };

      if (this.damage > c.damage) {
        this.damageHigher = true;
        this.cDamageHigher = false;
      } else if (this.damage < c.damage) {
        this.damageHigher = false;
        this.cDamageHigher = true;
      } else if (this.damage == c.damage) {
        this.damageHigher = null;
        this.cDamageHigher = null;
      };

      if (this.hull > c.hull) {
        this.hullHigher = true;
        this.cHullHigher = false;
      } else if (this.hull < c.hull) {
        this.hullHigher = false;
        this.cHullHigher = true;
      } else if (this.hull == c.hull) {
        this.hullHigher = null;
        this.cHullHigher = null;
      };
    },
  }
};
</script>

