<!-- Research: https://www.sitepoint.com/creating-beautiful-charts-vue-chart-js/ -->

<script>
import { Bar, mixins } from 'vue-chartjs';
import { mapGetters } from 'vuex';

const { reactiveProp } = mixins;

export default {
  name: 'galChart',
  mixins: [reactiveProp],
  extends: Bar,
  data () {
    return {
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true,
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    this.renderChart(this.chartData, this.options);
  }
};
</script>
